/* pager */
.cycle-pager {
	text-align: center;
	width: 100%;
	z-index: 999999;
	position: absolute;
	bottom: 45px;
	overflow: hidden;
	/*background: #fff;*/
	height: 35px;
}
.cycle-pager span {
    font-family: arial;
    font-size: 50;
    width: 16px;
    height: 16px;
    display: inline-block;
    background: #2941b2;
    cursor: pointer;
    border-radius: 16px;
    border: 2px solid #2941b2;
    margin-top: 6px;
		margin-right: 10;
		box-shadow: 0 0 5px rgb(255,255,255);
}
.cycle-pager span.cycle-pager-active {
	background: #FFF;
	box-shadow: 0 0 5px rgb(0, 0, 0);
	border: 2px solid #941e3d;
}
.cycle-pager > * { cursor: pointer;}

